html, body, .root {
  height: 100%;
}

body {
  line-height: 1.5;
  padding-top:55px !important;
}

body, .app {
  background: white;
}

/* main page */

.list-books-title {
  padding: 10px 0;
  /* background: #009eef; */
  background: rgb(19, 0, 36);
  background: linear-gradient(90deg, rgba(19, 0, 36, 1) 0%, rgba(0, 158, 239, 1) 39%, rgba(0, 212, 255, 1) 100%);
  text-align: center;
}

.list-books-title h1 {
  font-weight: 400;
  margin: 0;
  color: white;
}

.list-books-content {
  padding: 0 0 80px;
  flex: 1;
}

.bookshelf {
  padding: 0 10px 20px;
  margin-bottom: 60px;
}

@media (min-width: 600px) {
  .bookshelf {
    padding: 0 20px 40px;
  }
}
@media (max-width: 900px) {
  .smooth-link {
    font-size:13px;
    margin-top:25px;
  }
  .list-books-title .left-side h1 {
    font-size:25px;
    top:30px;
  }
  .left-side {
    padding-top:17px;
  }
  .right-side {
    padding-right:15px;
  }
  .bucket-container {
    width: 80vw;
    left: calc(50% - 40vw);
  }
  .cat-name-button {
    font-size:12px;
  }
  .move-all {
    font-size:12px;
  }
  .remove-all {
    width:15px;
    height:15px;
    top:30px;
  }
}

.bookshelf-title {
  border-bottom: 1px solid #dedede;
  padding-top: 15px;
}

.bookshelf-books {
  text-align: center;
}

.open-search {
  position: fixed;
  right: 25px;
  bottom: 25px;

}

.open-search button {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #009eef;
  background-image: url('./icons/add.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 28px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  font-size: 0;
  outline: 0;
  cursor: pointer;
}

.open-search button:hover {
  background-color: rgb(0, 158, 239, .5);
}


/* search page */

.search-books-bar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  display: flex;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 0 6px rgba(0, 0, 0, 0.23);
}

.search-books-input-wrapper {
  flex: 1;
  background: #e9e;
}

.search-books-bar input {
  width: 100%;
  padding: 15px 10px;
  font-size: 1.25em;
  border: none;
  outline: none;
}

.close-search {
  display: block;
  top: 20px;
  left: 15px;
  width: 50px;
  height: 53px;
  background-image: url('./icons/arrow-back.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 28px;
  font-size: 0;
  border: none;
  outline: none;
}

button:active {
  border: red;
  outline: none;
}

.search-books-results {
  padding: 95px 10px 20px;
}

/* books grid */

.books-grid {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.books-grid li {
  padding: 10px 15px;
  text-align: left;
}

.book {
  width: 140px;
}

.book-title,
.book-authors {
  font-size: 0.8em;
}

.book-title {
  margin-top: 10px;
}

.book-authors {
  color: #999;
}

.book-top {
  position: relative;
  height: 200px;
  display: flex;
  align-items: flex-end;
}

.book-shelf-changer {
  cursor: pointer;
  position: absolute;
  top: -5px;
  right: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-image: url('./icons/three-dots.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  /* box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23); */
}

.book-shelf-changer select {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

/* book cover */

.book-cover {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background: #eee;
}

.book-cover-title {
  padding: 20px 10px 0;
  text-align: center;
  font-size: 0.8em;
}


.selected-shelf-mark {
  display: block;
  width: 15px;
  height: 15px;
  background-image: url('./icons/check-mark.svg');
  background-repeat: no-repeat;
  background-position: center;
}